import axios from 'axios';

export const FETCH_WORK = 'FETCH_WORK';

export const workData = (data) => {
    return {
        type: FETCH_WORK,
        data
    }
}

export const fetchWork = () => {
  return (dispatch) => {
      return axios.get(`/work/list.json`)
      .then(response => {
          dispatch(workData(response.data))
      })
      .catch(error => {
          throw(error);
      });
  }
}
